import Vue from "vue";
import Vuex from 'vuex'
import {requestData} from "@/utils/DataRequestUtil";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        ItemSource: {},
        SkillSource: {},
        SuitSource: {},
        SkillConfigSource: {},
        TitleConfigSource: {},
    },
    getters: {
    },
    mutations: {
        setItemSource(state, data) {
            state.ItemSource = data
        },
        setSkillSource(state, data) {
            state.SkillSource = data
        },
        setSuitSource(state, data) {
            state.SuitSource = data
        },
        setSkillConfigSource(state, data) {
            state.SkillConfigSource = data
        },
        setTitleConfigSource(state, data) {
            state.TitleConfigSource = data
        }
    },
    actions: {
        initStore({commit}) {
            return Promise.all([
                requestData("item").then(res => commit("setItemSource", res)),
                requestData("skill").then(res => commit("setSkillSource", res)),
                requestData("suit").then(res => commit("setSuitSource", res)),
                requestData("skill_config").then(res => commit("setSkillConfigSource", res)),
                requestData("title_config").then(res => commit("setTitleConfigSource", res)),
            ])
        }
    }
})