export function convertValue(item) {
    const newItem = {}
    Object.entries(item).forEach(([key, value]) => {
        newItem[key] = skillFieldDef[key] ? skillFieldDef[key](value) : value
    })
    // 总属性
    newItem['suitAttr'] = calcSuitAttr(item)
    newItem['totalAttr'] = newItem['suitAttr'] + calcPartAttrs(item)
    return newItem
}

function calcSuitAttr(item) {
    return Number(item['atk'])
        + Number(item['def'])
        + Number(item['agi'])
        + Number(item['fs'])
        + Number(item['ss'])
        + Number(item['ws'])
        + Number(item['rs'])
        + Number(item['ts'])
}

function calcPartAttrs(item) {
    return calcItemAttr(searchItemText(item['weaponId']))
        + calcItemAttr(searchItemText(item['armorId']))
        + calcItemAttr(searchItemText(item['petId']));
}